#content {
  padding: 10%;
  max-width: 700px;
}
#contact-details span {
  white-space: nowrap;
}
.section-title {
  border-left: 3px solid #1FA2D6;
  padding-left: 0.5em;
}
.job-description .list-item {
  padding-left: 1em;
  display: inline-block;
  position: relative;
}
.job-description .list-item:before {
  position: absolute;
  left: 0em;
  content: '○';
}
